.main-content-qa {
  max-width: 1024px;
  width: 100%;
  padding: 4rem 0rem;
  padding-bottom: 0rem;
  margin-bottom: 0;
  margin: 0 auto;
  @media only screen and (max-width: 800px) {
    max-width: 100%;
    width: auto;
    padding: 4rem 1rem;

  }
  &__title {
    font-family: $cochin;
    text-align: center;
    background-color: rgba(255, 255, 255, 0.4);
    padding-top: 2.9rem;
    padding-bottom: 2rem;
    font-size: 2.6rem;
  }
  .entry-content {
    font-family: $monseratt;
    font-size: 1.6rem;
    line-height: 30px;
    background-color: rgba($white, 0.4);
    padding: 2rem 2.5rem;
    p {
      font-family: 600;
      font-size: 1rem;
      margin-bottom: 1.2rem;
    }
    ul li {
      font-size: 1rem;
    }
  }
  footer {
    margin-top: 4rem;
  }
}



