//colors
$nude: #c79b7e;
$white: #fff;
$black: #000;
$brown: #7C5046;
//fonts

@font-face {
    font-family: 'ciclegordita_italic';
    src: url('/fonts/cicle_gordita_italic-webfont.woff2') format('woff2'),
         url('/fonts/cicle_gordita_italic-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'ciclegordita';
    src: url('/fonts/cicle_gordita-webfont.woff2') format('woff2'),
         url('/fonts/cicle_gordita-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'cochin';
    src: url('/fonts/Cochin-LT-Font.ttf') format('ttf');
    font-weight: normal;
    font-style: normal;
}

$cochin: 'cochin';
$cicleGordita: 'ciclegordita';
$cicleGorditaItalic: 'ciclegordita_italic';
$monseratt: 'Montserrat', sans-serif;
$playFair: 'Playfair Display', serif;
$PTSerif: 'PT Serif', serif;



